/*
 * Стили компонента App
 */
.App {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 16px;
  padding-bottom: 24px;
}

.listContainer {
  display: flex;
  flex-direction: column;
}

.startLabel {
  padding-top: 50px;
  text-align: center;
  font-size: 20px;
  font-style: italic;
  color: #303f9f;
}

